.rackcabinet {
  width: 90%;
  background-color: grey;
  margin: 0 auto;
  margin-top:15px;
  padding:0 2px 0 2px;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack:center;
      justify-content:center;
  border-radius:10px;
}
.rack {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack:center;
      justify-content:center;
  width:96%;
  -ms-flex-wrap:wrap;
      flex-wrap:wrap;
}
.drumRack {
  width: 100%;
  margin: 10px 6px;
  padding:4px 2px 4px 2px;
  border-radius: 9px;
  display: -ms-flexbox;
  display: flex;
  position:relative;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap:wrap;
      flex-wrap:wrap;
  background-color: #fafafa ;
}

.bpmplaybar{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-pack:center;
      justify-content:center;
}

textarea:focus, input:focus{
  outline: none;
}

@media (min-width: 1900px){
  div.rackcabinet{
    margin-top: 80px !important;
    margin-bottom: 60px; 
  }

  .lightOff, .lightOn {
    left: 18px;
    width: 14px;
    height: 14px;
  }
}

@media only screen and (max-width: 1024px) {
  .lightOff, .lightOn {
    width: 8px;
    height: 8px;
    left: 9px;
  }
}

@media only screen and (max-width: 960px) {
  .lightOff, .lightOn {
    width: 10px;
    height: 10px;
    left: 10px;
  }
  div.rackcabinet{
    margin-top: 60px;
    margin-bottom: 60px;
  }
}

@media only screen and (max-width: 860px) {
  .lightOff, .lightOn {
    left: 5px;
    width: 8px;
    height: 8px;
  }
}

@media only screen and (max-width: 640px) {
  .lightOff, .lightOn {
    left: 5px;
    width: 8px;
    height: 8px;
  }
}



