.progressbar {
  width:95%;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-pack:center;
      justify-content:center;
}

.progresslight {
  width: 40px;
  height: 10px;
  position:relative;
  margin:5px;
  border-radius: 2px;
  background-color: #9e9e9e;
}

.progresslighton {
  width: 40px;
  height: 10px;
  margin:5px;
  position:relative;
  border-radius: 2px;
  background-color: #f44336 ;
}

@media (min-width: 1900px){
  .progresslight{
    width: 50px;
  }
  .progresslighton {
    width: 50px;
  }
}


